}

/* –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä –¥–ª—è –∫–∞—Ä—Ç–æ—á–µ–∫ */
.brand-stats {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 1rem;
  margin-bottom: 2rem;
}

/* –ö–∞—Ä—Ç–æ—á–∫–∞ —Å —Ä–µ–π—Å–∞–º–∏ (—á–µ—Ä–Ω—ã–π —Ñ–æ–Ω) */
.brand-card:nth-child(1),
.brand-card:nth-child(4),
.brand-card:nth-child(7) {
  background: #171717; /* –ß–µ—Ä–Ω—ã–π */
  color: #fff;
}

/* –ö–∞—Ä—Ç–æ—á–∫–∞ —Å —Ä–µ–π—Å–∞–º–∏ (—Å–µ—Ä—ã–π —Ñ–æ–Ω) */
.brand-card:nth-child(2),
.brand-card:nth-child(5),
.brand-card:nth-child(8) {
  background: #555; /* –°–µ—Ä—ã–π */
  color: #f0f0f0;
}

/* –ö–∞—Ä—Ç–æ—á–∫–∞ —Å —Ä–µ–π—Å–∞–º–∏ (–±–µ–ª—ã–π —Ñ–æ–Ω, —á–µ—Ä–Ω—ã–π —Ç–µ–∫—Å—Ç) */
.brand-card:nth-child(3),
.brand-card:nth-child(6),
.brand-card:nth-child(9) {
  background: #fff; /* –ë–µ–ª—ã–π */
  color: #111;
  border: 1px solid #ccc;
}

/* –û–±—â–∏–π —Å—Ç–∏–ª—å –¥–ª—è –≤—Å–µ—Ö –∫–∞—Ä—Ç–æ—á–µ–∫ */
.brand-card {
  padding: 1rem;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  text-align: left;
  width: 85%;
  max-width: 300px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

/* –ù–∞–∑–≤–∞–Ω–∏–µ –≥—Ä—É–∑–æ–≤–∏–∫–∞ */
.brand-title {
